import { IEditorUi } from "../../ui/IEditorUi";
import { KeyCodes } from "../../util/KeyCodes";
import { Action } from "../Action";
import { ActionKeys } from "../ActionKeys";

export class ViewGridAction extends Action {
    constructor(editorUi: IEditorUi) {
        super(ActionKeys.pmf_grid, editorUi, true);

        this.shortcutControlKey = true;
        this.shortcutShiftKey = true;
        this.shortcutKeyCode = KeyCodes.G;

        this.setToggleAction(true);
    }

    isSelected(): boolean {
        return this.editorUi.graph.isGridEnabled();
    }

    execute(evt?: PointerEvent | KeyboardEvent): void {
        let graph = this.editorUi.graph;
        graph.setGridEnabled(!graph.isGridEnabled());
        graph.refresh();
    }
}